.container {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.container span {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px;
  min-width: 40px;
  min-height: 40px;
  border-radius: 4px;
  cursor: pointer;
}

.container span.active {
  background: #d57689;
  cursor: default;
}

.container span:hover {
  opacity: 0.5;
}

.container span.active:hover {
  opacity: 1;
}

.container span.hidden {
  display: none;
}